<template>
    <div class="window">
        <div class="window__head">
            <div class="window__head-box">
                <img :src="avatars" alt="" class="window__head-box-avatar">
                <p class="window__head-box-title">
                    {{ name }}
                    <span class="window__head-box-online">{{ status }}</span>
                </p>
            </div>
        </div>
        <div class="window__body">
            <div class="window__body-box">
                <TransitionGroup name="msg">
                <div v-for="(message, i) in messages" :key="i" class="window__body-box-sent"
                :class="{ send: message.id == this.id, receive: message.id != this.id }"
                >
                    <div class="window__body-box-sent-container">
                        <p class="window__body-box-sent-text">{{ message.msg }}</p>
                        <img class="window__body-box-sent-photo" v-if="message.url" :src="message.url" alt="">
                        <p class="window__body-box-sent-text">{{ message.comment }}</p>
                    </div>
                <span class="window__body-box-sent-date">{{ message.time }}</span>
                </div>
            </TransitionGroup>
            
            </div>
        </div>
        <div class="window__send">
            <div class="window__send-box">
                <input type="text" placeholder="Написать сообщение..." class="window__send-input" v-model="text">
                <button v-if="text < 1" @click="openModal" class="window__send-photo"><img src="@/assets/img/photo.svg" alt=""></button>
                <button v-else @click="sendMsg" class="window__send-commit"><img src="@/assets/img/send.svg" alt=""></button>
            </div>
        </div>
    </div>
</template>

<script>
    export default {

        props: {
            name: {
                type: String
            },
            status: {
                type: String
            },
            avatars: {
                type: Object
            },
            id: {
                type: Number
            },
            messages: {
                type: Array
            },
            view: {
                type: Boolean
            }
        },
        data() {
            return {
                text: '',
                isActive: true,
                }
            },

        methods: {
            sendMsg() {
                const hours = new Date().getHours() < 10 ? '0' + new Date().getHours() : new Date().getHours();
                const minutes = new Date().getMinutes() < 10 ? '0' + new Date().getMinutes() : new Date().getMinutes();
                const time = `${hours}:${minutes}`
                if (this.text.length > 0) {
                    const mess = {
                        msg: this.text,
                        id: this.id,
                        time: time
                    }
                    this.$emit('newMsg', mess)
                    this.text = ''
                }
            },
            openModal() {
                this.$emit('changeModal', this.id)
            },
        }

        }
</script>
