<template>
    <div class="modal">
        <div class="modal__block">
            <h2 class="modal__block-title">
                Отправить картинку
            </h2>
            <div class="modal__block-inputs">
                <label>
                    <span>URL</span>
                    <input v-model="url" type="text" placeholder="URL">
                </label>
                <label>
                    <span>Комментарий</span>
                    <textarea v-model="comment" type="text" placeholder="Комментарий"></textarea>
                </label>
            </div>
            <div class="modal__block-btns">
                <button @click="closeModal" class="modal__block-btns-btn red">Отмена</button>
                <button @click="sendPhoto" class="modal__block-btns-btn purple">Отправить</button>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        props: {
            view: {
                type: Boolean
            },
            id: {
                type: Number
            }
        },

        data() {
            return {
                url: '',
                comment: ''
            }
        },
        methods: {
            closeModal() {
                this.$emit('changeModal')
            },
            sendPhoto() {

                const hours = new Date().getHours() < 10 ? '0' + new Date().getHours() : new Date().getHours();
                const minutes = new Date().getMinutes() < 10 ? '0' + new Date().getMinutes() : new Date().getMinutes();
                const time = `${hours}:${minutes}`;

                if(this.url.length > 0 && this.comment.length > 0) {
                    const photoMsg = {
                    url: this.url,
                    comment: this.comment,
                    id: this.id,
                    time: time
                }
                this.$emit('newPhoto', photoMsg)
                this.comment = ''
                this.url = ''
                }
                this.closeModal()
            }
        }
    }
</script>

