<template>
  <div class="body">
    <Window 
    :avatars="avatars.alex" 
    :name="'Александр'" 
    :status="'Онлайн'" 
    :id="1"
    :messages="messages"
    @newMsg="newMsg"
    :view="view"
    @changeModal="changeModal"
    ></Window>

    <Window 
    :avatars="avatars.eugene" 
    :name="'Евгений'" 
    :status="'Онлайн'" 
    :id="2"
    :messages="messages"
    @newMsg="newMsg"
    :view="view"
    @changeModal="changeModal"
    ></Window>
    <Transition name="modal">
    <Modal
    v-if="view"
    :view="view"
    :id="id"
    @changeModal="changeModal"
    @newPhoto="newPhoto"
    ></Modal>
  </Transition>
  </div>
</template>

<script>
import Modal from '@/components/Modal.vue';
import Window from './components/Window.vue';

  export default {
    components: { Window, Modal },

    data() {
      return {
        view: false,
        id: null,
        avatars: 
        { 
            alex: require(`@/assets/img/avatar-1.png`),
            eugene: require(`@/assets/img/avatar-2.png`) 
        },
        messages: [],
    }
    },
    mounted() {
            const msgStorage = localStorage.messages ? JSON.parse(localStorage.messages) : [];
            this.messages = msgStorage
        },

    methods: {
      newMsg(msg) {
        this.messages.push(msg);
        localStorage.messages = JSON.stringify(this.messages)
      },
      newPhoto(photoMsg) {
        this.messages.push(photoMsg)
        localStorage.messages = JSON.stringify(this.messages)
      },

      changeModal(id) {
        this.view = !this.view
        this.id = id
      },
    }
}
</script>
